const httpCoreUtils = require('./../../common/httpCoreUtils');
//import httpCoreUtils from './../../common/httpCoreUtils.js';
/*基础http通用接口*/
function getDolls(uid) {
	return httpCoreUtils.fetch('api/userDoll/getDolls',{userId:uid});
}

function getWin(uid,busId) {
	return httpCoreUtils.fetch('api/share/win',{uid:uid,busId:busId});
}

function getInvite(uid) {
	return httpCoreUtils.fetch('api/share/inviteCode',{uid:uid});
}

function getChargeRank(uid) {
	return httpCoreUtils.fetch('api/newyearRecharge/getRanking',{uid:uid});
}

function getStatus(uid) {
	return httpCoreUtils.fetch('api/newyearRecharge/getStatus',{uid:uid});
}
module.exports = {
  getDolls: getDolls,
  getWin:getWin,
  getInvite:getInvite,
  getChargeRank:getChargeRank,
  getStatus:getStatus
}



