/*基础http通用接口*/
define([], function() {
  //var baseUrl = "http://www.nuannuanzhibo.com/";
  var getDateAdd = function(AddDayCount) {
    var dd = new Date();
    dd.setDate(dd.getDate() + AddDayCount); //获取AddDayCount天后的日期 
    var y = dd.getFullYear();
    var m = dd.getMonth() + 1; //获取当前月份的日期 
    var d = dd.getDate();
    return y + "-" + m + "-" + d;
  }

  var getDateAddByString = function(dateStr, AddDayCount) {
    var dd = convertDateFromString(dateStr);
    dd.setDate(dd.getDate() + AddDayCount); //获取AddDayCount天后的日期 
    var y = dd.getFullYear();
    var m = dd.getMonth() + 1; //获取当前月份的日期 
    var d = dd.getDate();
    return y + "-" + m + "-" + d;
  }

  var convertDateFromString = function(dateString) {
    if (dateString) {
      var arr1 = dateString.split("-");
      var date = new Date(arr1[0], parseInt(arr1[1]) - 1, arr1[2]);
      return date;
    } else {
      return new Date();
    }
  }

  var getTimestamp = function() {
    // 获取当前时间戳(以s为单位)
    var timestamp = Date.parse(new Date());
    timestamp = timestamp / 1000;
    return timestamp;
  }

  function fmtDate(obj) {
    let dobj=obj;
    let date = new Date(parseInt(dobj));
    var y = 1900 + date.getYear();
    var m = "0" + (date.getMonth() + 1);
    var d = "0" + date.getDate();
    var h= "0" +date.getHours(); 
var minute= "0" +date.getMinutes(); 
var second= "0" +date.getSeconds(); 
    return y + "-" + m.substring(m.length - 2, m.length) + "-" + d.substring(d.length - 2, d.length)+" "+h.substring(h.length - 2, h.length)+":"+minute.substring(minute.length - 2, minute.length)+":"+second.substring(second.length - 2, second.length);
  }

  var reload = function() {
    window.location.reload();
  }




  return {
    getDateAdd: getDateAdd,
    convertDateFromString: convertDateFromString,
    getDateAddByString: getDateAddByString,
    getTimestamp: getTimestamp,
    reload: reload,
    fmtDate:fmtDate,
  };
});



