<template>
  <section class="maxBox">
    <img src="http://lianai-image-sys.qiniudn.com/wa/code-bg.png" alt="" class="s_bg">
    <div class="codeBox">
      <div class="codeBoxIn">
        <span class="codeHead"><img :src="info.head" alt=""></span>
        <!-- 头像 -->
        <p class="codeName">{{info.name}}</p>
        <!-- 昵称 -->
        <div class="type_1" v-show="info.code>0">
          <p class="codeTXt">我的邀请码</p>
          <p class="code">{{info.code}}</p>
          <div class="copy" @click="copyToClipboard">复制邀请</div>
        </div>
        <div class="type_2" v-show="info.code==0">邀请你一起免费抓娃娃~又萌又有趣~</div>
      </div>
      <div class="rules">下载即可<em>免费玩3次</em>，输入邀请码<em>再免费玩3次</em>，分享给好友最高获600币！</div>
    </div>
    <div class="footer s_footer"><img src="http://lianai-image-sys.qiniudn.com/wa/footer.png" alt="" @click="jumpDownload"></div>
  </section>
</template>
<script>
import user from './assets/js/user.js'
import doll from './api/doll/doll'
import common from './common/common.js'
import httpUtils from './common/httpUtils.js'

export default {
  name: 'app',
  components: {},
  data() {
    return {
      uid: 1832306,
      did: 1,
      info: {},
    }
  },
  filters: {
    fmtDate: function(value) {
      if (!value) return ''
      return common.fmtDate(value);
    }
  },
  created: function() {
    this.uid = httpUtils.request("uid");
    console.log(this.uid);
    let self = this;
    doll.getInvite(this.uid).then(function(res) {
      console.log(res);
      self.info = res.data;
    }).catch(function(err) {
      console.log(err);
    });
  },
  methods: {
    jumpDownload: function() {
      httpUtils.jumpDownloadUrl();
    },
    copyToClipboard: function() {
      let text=this.info.code;
      var textArea = document.createElement("textarea");
      textArea.style.position = 'fixed';
      textArea.style.top = '0';
      textArea.style.left = '0';
      textArea.style.width = '2em';
      textArea.style.height = '2em';
      textArea.style.padding = '0';
      textArea.style.border = 'none';
      textArea.style.outline = 'none';
      textArea.style.boxShadow = 'none';
      textArea.style.background = 'transparent';
      textArea.value = text;
      document.body.appendChild(textArea);
      textArea.select();

      try {
        var successful = document.execCommand('copy');
        var msg = successful ? '成功复制到剪贴板' : '该浏览器不支持点击复制到剪贴板';
      } catch (err) {
      }
      document.body.removeChild(textArea);
    }

  }
}

</script>
<style>


</style>



