//import httpUtils from './httpUtils.js'

const httpUtils = require('./httpUtils.js')
/*基础http通用接口*/

    function getUid() {
        if (httpUtils.isShareVisit()) {
            return 10000;
        }else{
            return getParamWithCookies('uid');
        }
    }

    function getTid() {
        if (httpUtils.isShareVisit()) {
            return 10000;
        }else{
            return getParamWithCookies('tid');
        }
    }

    function getLoginKey() {
        return getParamWithCookies('loginKey');
    }

    function getDefaultHead() {
        return 'http://lianai-image-sys.qiniudn.com/live_web/logo_256.png';
    }

    function queryUser(callback,uid) {
        $.getJSON(httpUtils.getBaseUrl() + 'WebEntry.web', {
                cmd: 'cmd.webfollow',
                action: 'getUserInfo',
                uid: uid
            },
            function(data) {
                callback(data);
            });
    }

    function jumpUserInfo(uid) {
        if (httpUtils.isShareVisit()) {
            jumpDownloadUrl();
        }else{
            if (uid>0) {
                window.location.href = "http://www.xiehou360.com/7@#"+uid;
            }
        }
    }

    function jumpLive(lid) {
        if (httpUtils.isShareVisit()) {
            jumpDownloadUrl();
        }else{
            if (lid>0) {
                window.location.href = "http://www.xiehou360.com/1@#"+lid;
            }
        }
    }

    function jumpCharge() {
        if (httpUtils.isShareVisit()) {
            jumpDownloadUrl();
        }else{
            window.location.href = "http://t.xiehou360.com/2@#" + 1;
        }
    }

    function jumpBus(busId) {
        if (httpUtils.isShareVisit()) {
            jumpDownloadUrl();
        }else{
            window.location.href = "http://t.xiehou360.com/4@#" + busId;
        }
    }

    function jumpDownloadUrl() {
        window.location.href = httpUtils.getDownloadUrl();
    }

    function getParamWithCookies(paramName) {
        var paramValue = httpUtils.request(paramName);
        if (paramValue == null || paramValue == undefined || paramValue == '') {
            paramValue = httpUtils.getCookie(paramName);
        } else {
            httpUtils.setCookie(paramName, paramValue);
        }
        return paramValue;
    }

    var shareDom='<div class="float_bar" style="width: 100%;background-color: white;max-width: 640px;text-align: left;"><img class="float_bar_img " src="http://lianai-image-sys.qiniudn.com/e20170411/bottom_bar.png" style=" width: 100%;position: fixed;max-width: 640px;z-index: 999;" /><div class="float_bar_area" style="width: 100%;max-width: 640px;"><img class="float_bar_area_content " src="http://lianai-image-sys.qiniudn.com/e20170411/bottom_bar.png" style="width: 100%;opacity: 0;" /></div></div>';
    if (httpUtils.isShareVisit()) {
        var divObj = document.createElement("a");
        divObj.innerHTML=shareDom;
        divObj.href=httpUtils.getDownloadUrl();
        var first=document.body.firstChild;//得到页面的第一个元素 
        var wraphtml=document.body.insertBefore(divObj,first);
    }


module.exports = {
getUid: getUid,
        getLoginKey: getLoginKey,
        queryUser:queryUser,
        jumpUserInfo:jumpUserInfo,
        jumpLive:jumpLive,
        jumpCharge:jumpCharge,
        jumpDownloadUrl:jumpDownloadUrl,
        getDefaultHead:getDefaultHead,
        jumpBus:jumpBus
}


